<script lang="ts">
    import {FilterDefinition} from "../../types";
    import {getTranslation} from "../../helpers/translations";
    import {createEventDispatcher} from "svelte";

    export let value: null | string;
    export let filter: FilterDefinition = {column: '', type: 'date', label: '', remote_values: false}

    export const toggle = (toggle: boolean) => {
        disabled = !toggle;
    }

    const dispatch = createEventDispatcher();

    const handleChangedValue = () => {
        dispatch( 'change' );
    }

    let disabled = false;
    let element: HTMLElement;
    let i18n = getTranslation();
    let inputName = `acp_filter[${filter.column}]`;

</script>
<div class="acp-filter-group -date -single" bind:this={element}>
	<span class="acp-filter-group__text">
		{filter.label}
	</span>
	<div class="acp-filter-group__input">
		<input
				name={inputName}
				type="date"
				{disabled}
				on:change={handleChangedValue}
				placeholder={filter.label}
				bind:value={value}
				class:-active={value}

		>
	</div>
</div>
