<script lang="ts">
    import {FilterDefinition, FilterRuleDefinition} from "../../types";
    import {getTranslation} from "../../helpers/translations";
    import {createEventDispatcher} from "svelte";
    import AcDateModal from "ACUi/acui-date-picker/AcDateModal.svelte";
    import AcDropdown from "ACUi/acui-dropdown/AcDropdown.svelte";
    import FilterDropdownRemote from "./FilterDropdownRemote.svelte";

    export let value: null | string;
    export let filter: FilterDefinition = {column: '', type: 'date', label: '', remote_values: false}
    export let rule: FilterRuleDefinition;

    export const toggle = (toggle: boolean) => {
        disabled = !toggle;
    }

    const pattern = '[0-9]{4}-[0-9]{2}';
    const dispatch = createEventDispatcher();

    let dropdown: AcDropdown;
    let input: HTMLInputElement;
    let disabled = false;
    let element: HTMLElement;
    let i18n = getTranslation();
    let inputName = `acp_filter[${filter.column}]`;

    const handleChange = () => {
        dropdown.toggle();
        dispatch('change');
    }


</script>

{#if filter.remote_values}
	<FilterDropdownRemote
			filter={filter}
			rule={rule}
			bind:value={value}
			on:change
	></FilterDropdownRemote>
{:else }

	<div class="acp-filter-group -date -month">
		<span class="acp-filter-group__text">
			{filter.label}
		</span>
		<div class="acp-filter-group__input">
			<AcDropdown --menu-z-index="30000" closeOnClick={true} bind:this={dropdown}>
				<input type="text" bind:value={value} slot="trigger" name={inputName} bind:this={input} {pattern}/>
				<AcDateModal bind:value={value} on:change={ handleChange }/>
			</AcDropdown>
		</div>
	</div>

{/if}
