<script lang="ts">
    import {FilterDefinition} from "../../types";
    import {getTranslation} from "../../helpers/translations";
    import {createEventDispatcher, onMount} from "svelte";
    import {acWait} from "../../../../core/js/helper/global";

    export let value: null|Array<any> = [];
    export let filter: FilterDefinition = {column: '', type: 'numeric', label: ''}
    export const toggle = (toggle: boolean) => {
        disabled = !toggle;
    }

    const dispatch = createEventDispatcher();

    let element: HTMLElement;
    let disabled = false;
    let i18n = getTranslation();
    let inputNameMin = `acp_filter[${filter.column}][]`;
    let inputNameMax = `acp_filter[${filter.column}][]`;

    let valueStart = Array.isArray(value) ? value[0] : '' ?? '';
    let valueEnd = Array.isArray(value) ? value[1] : '' ?? '';

    let startMin = null;
    let startMax = null;
    let endMin = null;
    let endMax = null;

    const handleEndDateChange = () => {
        startMax = valueEnd;
        startMin = null;
        updateValue();
    }

    const handleStartDateChange = () => {
        endMax = null;
        endMin = valueStart;
        updateValue();
    }

    const handleStartDateBlur = () => {
        if (valueStart && valueEnd && new Date(valueEnd) < new Date(valueStart)) {
            valueEnd = valueStart;
        }
        updateValue();
    }

    const updateValue = () => {
        value = valueStart || valueEnd
            ? [
                valueStart, valueEnd
            ] : null;
        dispatch('change');
    }

    const handleEndDateBlur = () => {
        if (valueStart && valueEnd && (new Date(valueStart) > new Date(valueEnd))) {
            valueStart = valueEnd;
        }
    }

    const formHandler = async () => {
        await acWait(100);
        element.closest('form')?.addEventListener('submit', (e) => {
            if (!valueStart && !valueEnd) {
                disabled = true;
            }
        });
    }

    onMount(async () => {
        formHandler();
    })
</script>


<div class="acp-filter-group -date -range" bind:this={element}>
	<span class="acp-filter-group__text">
		{filter.label}
	</span>
	<div class="acp-filter-group__input">
		<input
				type="date" name={inputNameMin}
				placeholder={i18n.label_start_date}
				min={startMin} max={startMax}
				pattern="\d{4}-\d{2}-\d{2}"
				{disabled}
				bind:value={valueStart}
				on:inputs={handleStartDateChange}
				on:blur={handleStartDateBlur}
		>
	</div>
	<span class="acp-filter-group__text">
		-
	</span>
	<div class="acp-filter-group__input">
		<input
				type="date" name={inputNameMax}
				placeholder={i18n.label_end_date}
				min={endMin} max={endMax}
				pattern="\d{4}-\d{2}-\d{2}"
				{disabled}
				bind:value={valueEnd}
				on:input={handleEndDateChange}
				on:blur={handleEndDateBlur}
		>
	</div>
</div>