<script lang="ts">
    import {FilterDefinition} from "../../types";
    import {getTranslation} from "../../helpers/translations";
    import {createEventDispatcher, onMount} from "svelte";
    import {acWait} from "../../../../core/js/helper/global";

    export let value: null | Array<any> = [];
    export let filter: FilterDefinition = {column: '', type: 'date', label: ''}

    export const toggle = (toggle: boolean) => {
        disabled = !toggle;
    }

    const dispatch = createEventDispatcher();

    let disabled = false;
    let element: HTMLElement;
    let i18n = getTranslation();
    let inputName = `acp_filter[${filter.column}][]`;

    let valueStart = Array.isArray(value) ? value[0] : '' ?? '';
    let valueEnd = Array.isArray(value) ? value[1] : '' ?? '';

    const handleStartChange = () => {
        if (parseInt(valueEnd) < parseInt(valueStart)) {
            valueEnd = valueStart;
        }
        updateValue()
    }

    const handleEndChange = () => {
        if (parseInt(valueStart) > parseInt(valueEnd)) {
            valueStart = valueEnd;
        }
        updateValue()
    }

    const updateValue = () => {
        value = valueStart || valueEnd
            ? [
                valueStart, valueEnd
            ] : null;

        dispatch('change');
    }

    const formHandler = async () => {
        await acWait(100);
        element.closest('form')?.addEventListener('submit', () => {
            if (!valueStart && !valueEnd) {
                disabled = true;
            }
        });
    }

    onMount(() => {
        formHandler();
    })

</script>

<div class="acp-filter-group" class:-active={value} bind:this={element}>
	<span class="acp-filter-group__text">
		{filter.label}
	</span>
	<div class="acp-filter-group__input">
		<input
				name={inputName} type="number" step="any"
				placeholder={i18n.label_start_number}
				{disabled}
				bind:value={valueStart}
				on:change={handleStartChange}
				on:blur={handleStartChange}
		>
	</div>
	<span class="acp-filter-group__text">
		-
	</span>
	<div class="acp-filter-group__input">
		<input
				name={inputName} type="number" step="any"
				placeholder={i18n.label_end_number}
				{disabled}
				bind:value={valueEnd}
				on:change={handleEndChange}
				on:blur={handleEndChange}
		>
	</div>
</div>