<script lang="ts">
    import {FilterDefinition, FilterRuleDefinition, SelectItemCollection} from "../../types";
    import {getTranslation} from "../../helpers/translations";
    import {createEventDispatcher, onMount} from "svelte";
    import FilterDropdownRemote from "./FilterDropdownRemote.svelte";

    export let value: null | string;
    export let filter: FilterDefinition = {column: '', type: 'date', label: '', remote_values: false}
    export let rule: FilterRuleDefinition;

    export const toggle = (toggle: boolean) => {
        disabled = !toggle;
    }

    const dispatch = createEventDispatcher();

    let disabled = false;
    let element: HTMLElement;
    let i18n = getTranslation();
    let inputName = `acp_filter[${filter.column}]`;
    let options = [];

    onMount(() => {
        if (value) {
            options = [
                value
            ];
        }
    });

    const itemMapper = (d: SelectItemCollection): SelectItemCollection => {
        let years = []
        let newOptions: SelectItemCollection = [];

        d.forEach(o => {
            const year = o.value?.substring(0, 4) ?? '';

            if (!Number.isNaN(parseFloat(year))) {
                if (years.indexOf(year) < 0) {
                    years.push(year);
                    newOptions.push({value: year, label: year, group: o.group});
                }
            } else {
                newOptions.push(o);
            }


        });


        return newOptions;
    }

</script>

{#if filter.remote_values}
	<FilterDropdownRemote filter={filter} itemMapper={itemMapper} bind:value={value} bind:rule={rule} on:change>
	</FilterDropdownRemote>
{:else}
	<input type="number" min="1900" max="3000" step="1" bind:value={value} name={inputName} placeholder={filter.label}>
{/if}
