<script lang="ts">
    import {FilterDefinition, FilterRuleDefinition, Select2Items, SelectItem, SelectItemCollection} from "../../types";
    import {getFilterOptionsForColumn} from "../../ajax/select-values";
    import {createEventDispatcher, onMount} from "svelte";
    import FiltersMapper from "../../mapping/filters-mapper";
    import Select from 'svelte-select';

    declare const jQuery: any;

    type ItemMapperType = ( items: SelectItemCollection ) => SelectItemCollection;

    export let value = '';
    export let filter: FilterDefinition
    export let rule: FilterRuleDefinition;
    export let itemMapper: ItemMapperType|null;

    export const toggle = (toggle: boolean) => {
        disabled = !toggle;
    }

    const dispatch = createEventDispatcher();

    let modelValue: SelectItem | null = null;
    let items: SelectItemCollection = [];
    let emptyLabel = 'Empty';
    let loading: boolean = false;
    let disabled: boolean = false;
    let hideEmptyState: boolean = false;
    let select: HTMLSelectElement;
    let inputName = `acp_filter[${filter.column}]`;

    onMount(() => {
        if (value) {
            modelValue = {
                value: value,
                label: rule?.label ?? value
            }

            items = [modelValue];
            getFilterOptionsForColumn(filter.column).then(retrievedItems => {
                items = mapItems( retrievedItems );
                loading = false;
            });
        } else {
            modelValue = null;
        }
    });

    const mapItems = ( items: Select2Items ): SelectItemCollection => {
        let options =  FiltersMapper.fromSelect2(items);

        if( itemMapper ){
            options = itemMapper( options );
		}

        return options;
	}

    const handleFocus = () => {
        if (items.length === 0) {
            loading = true;
            hideEmptyState = true;
            getFilterOptionsForColumn(filter.column).then(retrievedItems => {
                items = mapItems( retrievedItems );
                loading = false;
                hideEmptyState = false;
            }).catch((d: string) => {
                loading = false;
                emptyLabel = d;
                items = [];
                hideEmptyState = false;
            });
        }
    }

    const sanitizeLabel = (label) => {
        let el = document.createElement('div');
        el.innerHTML = label;
        return el.innerText;
    }

    const handleClearValue = () => {
        value = '';
        dispatch('change');
    }
    const handleChangedValue = (e) => {
        value = e.detail.value;
        dispatch('change');
    }
    const groupBy = (item) => item.group;

    $: disabled = value === '' || value === null;
    $: cssClasses = () => {
        let classes = ['-wordpress', '-acfilter'];

        if (!disabled) {
            classes.push('-active');
        }

        return classes.join(' ');
    }

    $: autoWidth = ((filter.label.length * 8) + 35);
    $: width = autoWidth < 200 ? '200px' : autoWidth.toString() + 'px';

</script>
<label class="acp-filter__label" for={inputName}>{filter.label}</label>
<input bind:value={value} name={inputName} id="{inputName}" type="hidden" {disabled}/>
<div title={modelValue?.label}>
	<Select {groupBy} class={cssClasses()} {items} {loading}
			placeholder={filter.label}
			showChevron
			listAutoWidth={false}
			{hideEmptyState}
			bind:value={modelValue}
			on:change={handleChangedValue}
			on:clear={handleClearValue}
			on:focus={handleFocus}
			--list-z-index="1000"
			--width={width}
	>
		<span slot="chevron-icon" class="dashicons dashicons-arrow-down-alt2" style="font-weight: 100; font-size: 15px; transform: translateY(3px)"></span>
		<span slot="clear-icon">
		<i class="dashicons dashicons-no-alt"></i>
	</span>
		<div slot="empty">
			<div style="padding: 10px;">{emptyLabel}</div>
		</div>

	</Select>
</div>