<script lang="ts">
    import {FilterDefinition, FilterRuleDefinition} from "../types";
    import FilterDropdown from "./Filter/FilterDropdown.svelte";
    import FilterDropdownRemote from "./Filter/FilterDropdownRemote.svelte";
    import FilterDropdownSearch from "./Filter/FilterDropdownSearch.svelte";
    import DateField from "./Filter/DateField.svelte";
    import DateMonths from "./Filter/DateMonths.svelte";
    import DateYears from "./Filter/DateYears.svelte";
    import RangedDate from "./Filter/RangedDate.svelte";
    import RangedNumber from "./Filter/RangedNumber.svelte";
    import TextSearch from "./Filter/TextSearch.svelte";
    import {initAcServices} from "AC/helpers/admin-columns";

    declare const jQuery: any;

    export let toggleCallback;
    export let value = ''
    export let filter: FilterDefinition
    export let rule: FilterRuleDefinition
    export const toggle = (show: boolean) => {
        toggleCallback(show);
    };

    export const getValue = () => {
        return value;
    }

    let types = [
        {type: 'select', component: FilterDropdown},
        {type: 'select_remote', component: FilterDropdownRemote},
        {type: 'select_search', component: FilterDropdownSearch},
        {type: 'date', component: DateField},
        {type: 'date_monthly', component: DateMonths},
        {type: 'date_yearly', component: DateYears},
        {type: 'date_range', component: RangedDate},
        {type: 'numeric', component: RangedNumber},
        {type: 'search', component: TextSearch}
    ];

    const changeHandler = () => {
        initAcServices().getService('ActiveFilters')?.refresh();
    }

    $: currentComponent = () => {
        let component = types.find(t => t.type === filter.type)

        return component ? component.component : null;
    }
</script>

<svelte:component this={currentComponent()} bind:value={value} filter={filter} rule={rule} bind:toggle={toggleCallback} on:change={changeHandler}/>