<script lang="ts">
    import {FilterDefinition, FilterRuleDefinition, SelectItem, SelectItemCollection} from "../../types";
    import {SearchFilterOptions} from "../../ajax/select-values";
    import {createEventDispatcher, onMount, tick} from "svelte";
    import FiltersMapper from "../../mapping/filters-mapper"; //
    import Select from 'svelte-select';
    import NoResults from "../../../../core/js/component/select/NoResults.svelte";
    import {getTranslation} from "../../helpers/translations";

    declare const jQuery: any;

    export let value = '';
    export let filter: FilterDefinition
    export let rule: FilterRuleDefinition;

    const dispatch = createEventDispatcher();
    const i18n = getTranslation();

    export const toggle = (toggle: boolean) => {
        disabled = !toggle;
    }

    let modelValue: SelectItem | null = null;
    let items: SelectItemCollection = [];
    let filterText = '';
    let loading: boolean = true;
    let disabled: boolean = false;
    let select: HTMLSelectElement;
    let inputName = `acp_filter[${filter.column}]`;
    let search = new SearchFilterOptions(filter.column);
    let firstEmptySearch = true;

    async function searchItems(d) {
        loading = true;
        if (d === '' && firstEmptySearch) {
            //loading = false;
            return [];
        }

        let data = await search.search(d);
        let result = FiltersMapper.fromSelect2(data.items);

        if (modelValue) {
            result.concat([modelValue]);
        }
        loading = false;

        return result;
    }

    onMount(() => {
        if (value) {
            modelValue = {
                value: value,
                label: rule?.label ?? value
            }
        } else {
            modelValue = null;
        }
    });


    const sanitizeLabel = (label) => {
        let el = document.createElement('div');
        el.innerHTML = label;
        return el.innerText;
    }

    const handleClearValue = () => {
        value = '';
        modelValue = null;
        dispatch( 'change' );
    }

    const handleChangedValue = (e) => {
        value = e.detail.value;
        dispatch( 'change' );
    }
    const handleFocus = async () => {
        if (filterText === '' && firstEmptySearch) {
            firstEmptySearch = false;
            filterText = ' ';
            await tick()

            filterText = '';
        }

    };
    const groupBy = (item) => item.group;

    $: disabled = value === '' || value === null;
    $: cssClasses = () => {
        let classes = ['-wordpress'];
        if (!disabled) {
            classes.push('-active');
        }

        return classes.join(' ');
    }

    $: autoWidth = ((filter.label.length * 8) + 35);
    $: width = autoWidth < 200 ? '200px' : autoWidth.toString() + 'px';
    $: emptyLabel = firstEmptySearch || loading  ? i18n.fetching_results : i18n.no_results;

</script>
<label class="acp-filter__label" for={inputName}>{filter.label}</label>
<input bind:value={value} name={inputName} id="{inputName}" type="hidden" {disabled}/>

<Select
		{groupBy}
		{filterText}
		class={cssClasses()}
		placeholder={filter.label}
		showChevron
		loadOptions={searchItems}
		listAutoWidth={false}
		debounceWait={600}
		bind:value={modelValue}
		on:change={handleChangedValue}
		on:clear={handleClearValue}
		on:focus={handleFocus}

		--list-z-index="1000"
		--width={width}
>
	<span slot="chevron-icon" class="dashicons dashicons-arrow-down-alt2" style="font-weight: 100; font-size: 15px; transform: translateY(3px)"></span>
	<span slot="clear-icon">
		<i class="dashicons dashicons-no-alt"></i>
	</span>
	<div slot="empty">
		<NoResults label={emptyLabel}></NoResults>
	</div>

</Select>
