import axios from "axios";
export const getFilterOptionsForColumn = (column) => {
    return new Promise((resolve, reject) => {
        axios.get(ajaxurl, {
            params: {
                action: 'acp-filtering-comparison-request',
                layout: AC.layout,
                column: column,
                _ajax_nonce: AC.ajax_nonce
            }
        }).then((result) => {
            switch (result.data.success) {
                case true:
                    resolve(result.data.data.results);
                    break;
                case false:
                    reject(result.data.data);
            }
        }).catch(d => {
            reject(d.message);
        });
    });
};
export class SearchFilterOptions {
    constructor(column) {
        this.column = column;
        this.cachedSearches = new Map();
    }
    search(query) {
        return new Promise((resolve, reject) => {
            if (this.cachedSearches.has(query)) {
                return resolve(this.cachedSearches.get(query));
            }
            axios.get(ajaxurl, {
                params: {
                    action: 'acp-filtering-comparison-request',
                    layout: AC.layout,
                    searchterm: query,
                    column: this.column,
                    list_screen: AC.list_screen,
                    _ajax_nonce: AC.ajax_nonce
                }
            }).then((result) => {
                switch (result.data.success) {
                    case true:
                        let results = {
                            items: result.data.data.results,
                            more: result.data.data.pagination.more
                        };
                        this.cachedSearches.set(query, results);
                        resolve(results);
                        break;
                    case false:
                        reject(result.data.data);
                }
            }).catch(d => {
                reject(d.message);
            });
        });
    }
}
